<template>
    <header :class="{login:isLogin, 'no-login':!isLogin}">
      <template v-if="!isLogin">
        <h1>Let's share</h1>
        <p>Vue   博客</p>
       <div class="btns">
        <router-link to="/login"><el-button >立即登录</el-button></router-link>
        <router-link to="/register"><el-button>注册账号</el-button></router-link>
      </div>  
      </template>
      <template v-if="isLogin">
        <h1><router-link to="/">Let's share</router-link></h1>
     <router-link to="/create"> <i class="el-icon-edit"></i></router-link>
      <div class="user">
        <img class="avatar" :src="user.avatar" :alt="user.username" :title="user.username">
        <ul>
          <li><router-link to="/my">我的</router-link></li>
          <li><a href="#" @click="onLogout">注销</a></li>
        </ul>
      </div>         
      </template>
    </header>
</template>

<script>
import auth from '../api/auth'
window.auth = auth

import {mapGetters,mapActions} from 'vuex'

export default {
  data(){
    return{ 
      
    }
  }, 
  computed:{
    ...mapGetters([
      'isLogin',
      'user'
    ])
  },
  create(){
    this.checkLogin()
  },
  methods:{
    ...mapActions([
      'checkLogin',
      'logout'
    ]),
     onLogout() {
        this.logout()
      }
  }
}
</script>



<style lang="less">
@import '../assets/base.less';

header.no-login {
  padding: 0 12% 30px 12%;
  background: @bgColor;
  display: grid;
  justify-items: center;
}
header h1 {
    color: #fff;
    font-size: 40px;
    margin: 60px 0 0 0;
    text-transform: uppercase;
    text-shadow: 20px 12px 4px rgb(0, 255, 149);
  }
  #app p {
    margin: 15px 0 0 0;
    color: rgb(255, 255, 255);
    font-size: 18px;
  } 
  .btns {
    margin-top: 20px;
  }
  button {
    margin: 20px 5px 0;
  }
  header.login {
  display: flex;
  align-items: center;
  background: @bgColor;

  h1 {
    margin: 0;
    padding: 0;
    font-size: 40px;
    text-transform: uppercase;
    flex: 1;

    a {
      color: #fff;
    }
 }

  .edit {
    color: #fff;
    font-size: 30px;
  }

  .avatar {
    width: 40px;
    height: 40px;
    border: 1px solid #fff;
    border-radius: 50%;
    margin-left: 15px;
  }

  .user {
    position: relative;

    ul {
      display: none;
      position: absolute;
      right: 0;
      list-style: none;
      border: 1px solid #eaeaea;
      margin:0;
      padding: 0;
      background-color: #fff;

      a {
        text-decoration: none;
        color: #333;
        font-size: 12px;
        display: block;
        padding: 5px 10px;

        &:hover {
          background-color: #eaeaea;
        }
      }

    }

    &:hover ul {
      display: block;
    }
  }
}
.el-icon-edit{
  color: rgb(255, 255, 255);
  font-size: 30px;
  cursor: pointer;
}
.el-icon-edit:hover{
  color: rgba(0, 255, 157, 0.767);
  font-size: 30px;
  cursor: pointer;
}
</style>


